;@import "global.scss";
.StatDayInMonth {
  text-align: center;
}

.StatDayInMonth-title {
  @include not-phone {
    font-size: $section_title_font_size_desktop;
    line-height: $section_title_font_line_height_desktop;
  }
  @include phone {
    font-size: $section_title_font_size_mobile;
    line-height: $section_title_font_line_height_mobile;
  }
}

.StatDayInMonth-subTitle {
  @include not-phone {
    font-size: $section_subtitle_font_size_desktop;
    line-height: $section_subtitle_font_line_height_desktop;
    margin-top: $section_subtitle_margin_top_desktop;
  }
  @include phone {
    font-size: $section_subtitle_font_size_mobile;
    line-height: $section_subtitle_font_line_height_mobile;
    margin-top: $section_subtitle_margin_top_mobile;
  }
}

.StatDayInMonth-items {
  margin-top: 20px;
  @include not-phone {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
}

.StatDayInMonth-item {
  @include not-phone {
    margin: 1px;
  }
  @include phone {
    display: flex;
    margin-top: 3px;
    align-items: center;
  }
}

.StatDayInMonth-itemDayNum {
  @include not-phone {
    transform: rotate(270deg);
    text-align: left;
  }

  @include phone {
    width: 20px;
    margin-right: 15px;
    text-align: right;
  }

  font-size: 12px;
}

.StatDayInMonth-item.is-selected {
  color: orange;

  .StatDayInMonth-itemColumn {
    opacity: 0.2;
  }
}

.StatDayInMonth-itemColumn {
  @include not-phone {
    margin-top: 20px;
  }

  &.is-horizontal {
    height: 20px;

    @include not-phone {
      display: none;
    }
  }

  &.is-vertical {
    width: 20px;

    @include phone {
      display: none;
    }
  }
}



